<template>
  <div class="btn">
    <el-button :type="type" :size="size" @click="click">
      <slot></slot>
    </el-button>
  </div>
</template>
<script>
export default {
  props: {
    type: {
      type: String,
      default: "text",
    },
    size: {
      type: String,
      default: "medium",
    },
    isPlain: {
      type: Boolean,
      default: true,
    },
  },
  methods: {
    click() {
      this.$emit("click");
    },
  },
};
</script>
<style scoped lang="scss">
.btn {
  display: inline-block;
}
.el-button--primary.is-plain {
  background-color: #ffffff;
  color: $color-primary;
}
.el-button--danger.is-plain {
  background-color: #ffffff;
}
</style>