<template>
  <div class="date">
    <el-date-picker
      v-model="searchDate"
      type="daterange"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期"
      :size="size"
      @change="changeDateSearch"
    >
    </el-date-picker>
  </div>
</template>
<script>
export default {
  props: {
      size:{
          type:String,
          default:'medium'
      }
  },
  data() {
    return {
      searchDate: "",
    };
  },
  methods: {
    changeDateSearch(data) {
      this.$emit("changeDateSearch", data);
    },
  },
};
</script>
<style scoped lang="scss">
.date {
  display: inline-block;
}
</style>