<template>
  <div style="height: 100%">
    <el-menu
      :default-active="selectedKey"
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose"
      :collapse="isCollapse"
      router
      @select="handleSelect"
    >
      <el-menu-item
        v-for="(item, index) in menuData"
        :key="index"
        :index="item.path"
      >
        <i :class="item.icon"></i>
        <span slot="title">{{ item.title }}</span>
      </el-menu-item>
    </el-menu>
  </div>
</template>
<script>
export default {
  data() {
    return {
      isCollapse: false,
      menuData: [
        { key: 1, title: "系统首页", icon: "el-icon-house", path: "/home" },
        {
          key: 2,
          title: "凭证管理",
          icon: "el-icon-document",
          path: "/voucherManage/index",
        },
        {
          key: 3,
          title: "发票管理",
          icon: "el-icon-collection-tag",
          path: "/invoiceManage/index",
        },
        // {
        //   key: 4,
        //   title: "账簿管理",
        //   icon: "el-icon-menu",
        //   path: "/ledgerManage/index",
        // },
        // {
        //   key: 5,
        //   title: "报表管理",
        //   icon: "el-icon-folder-opened",
        //   path: "/reportManage/index",
        // },
        {
          key: 6,
          title: "合同管理",
          icon: "el-icon-notebook-2",
          path: "/contractManage/index",
        },
        {
          key: 7,
          title: "文件管理",
          icon: "el-icon-date",
          path: "/resourceManage/index",
        },
        // {
        //   key: 8,
        //   title: "系统设置",
        //   icon: "el-icon-setting",
        //   path: "/sysSetting/index",
        // },
      ],
      selectedKey: "",
    };
  },
  created() {
    this.handleSelect("/home");
  },
  watch: {
    $route: function (to, from) {
      this.defaultCheck(this.menuData, to, from);
    },
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    // f5强制刷新时，默认点击当前路由对应的菜单
    defaultCheck(treeList, to, from) {
      this.$router.push({
        path: to.path,
      }).catch((err) => {
        // console.log("输出报错", err);
      });
      this.selectedKey = to.path;
      this.handleSelect(this.selectedKey)
    },
    // 手动点击
    handleSelect(index) {
      const tagData = this.menuData.find((item) => {
        return item.path === index;
      });
      let tagList = this.$store.getters.tagList || [];
      // 全部置为未选中状态，如果已存在则存在的项isActive=true
      let initTagList = [];
      if (tagList.length > 0) {
        tagList.forEach((value) => {
          let valJson = {
            ...value,
            isActive: value.path === index ? true : false,
          };
          initTagList.push(valJson);
        });
      }
      // 未出现在tag列表中
      if (initTagList.length > 0 && this.isExist(initTagList)) {
      } else {
        const activeTag = {
          isActive: true,
          ...tagData,
        };
        initTagList.push(activeTag);
      }
      this.$store.commit("setTagList", initTagList);
    },
    // 是否存在
    isExist(tagList) {
      const isExist = tagList.some((item) => {
        return item.isActive === true;
      });
      return isExist;
    },
  },
};
</script>
<style scoped lang="scss">
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  height: 100%;
}
</style>