<template>
  <div class="date">
    <el-input :size="size" v-model="searchInput" @change="changeInputSearch" :placeholder="placeholder"></el-input>
  </div>
</template>
<script>
export default {
  props: {
      size:{
          type:String,
          default:'medium'
      },
      placeholder:{
          type:String,
          default:'请输入'
      }
  },
  data() {
    return {
      searchInput: "",
    };
  },
  methods: {
    changeInputSearch(data) {
      this.$emit("changeInputSearch", data);
    },
  },
};
</script>
<style scoped lang="scss">
.date {
  display: inline-block;
}
</style>