<template>
    <div class="btn">
        <el-button :type="type" @click="click" :plain="isPlain" :size="size">
            <slot></slot>
        </el-button>
    </div>
</template>
<script>
export default {
    props:{
        type:{
            type:String,
            default:"primary"
        },
        size:{
            type: String,
            default:"medium"
        },
        isPlain: {
            type: Boolean,
            default: true
        }
    },
    methods:{
        click(){
            this.$emit("click")
        }
    }
}
</script>
<style scoped lang="scss">
.btn{
    display: inline-block;
}
.el-button--primary.is-plain{
    background-color: #ffffff;
    color: $color-primary;
}
.el-button--danger.is-plain{
    background-color: #ffffff;
}
</style>