<template>
  <el-dialog
    :title="title"
    :visible.sync="visible"
    :width="width"
    center
    destroy-on-close
    :before-close="handleClose"
  >
    <div class="c-h">
        <slot></slot>
    </div>
    <span slot="footer" v-if="!isPreview" class="dialog-footer">
      <SubmitBtn :type="'primary'" :isPlain="false" @click="submit"
        >确 认</SubmitBtn
      >
      <SubmitBtn class="header-btn-list" @click="cancel">取 消</SubmitBtn>
    </span>
    <span slot="footer" v-if="isPreview" class="dialog-footer">
      <SubmitBtn class="header-btn-list" @click="close">关 闭</SubmitBtn>
    </span>
  </el-dialog>
</template>

<script>
import SubmitBtn from "@/components/btn/btn.vue";
export default {
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    title: {
      type: String,
      default: "",
    },
    width:{
        type: String,
        default: '50%'
    },
    isPreview:{
      type: Boolean,
      default: false
    }
  },
  data() {
    return {};
  },
  components: { SubmitBtn },
  methods: {
    submit() {
      this.$emit("submit");
    },
    cancel() {
      this.$emit("cancel");
    },
    handleClose() {
      if(this.isPreview)this.close();
      else this.cancel();
    },
    close(){
      this.$emit("close")
    }
  },
};
</script>
<style scoped>
::v-deep .el-dialog__title{
  font-size: 14px;
  font-weight: 400;
  color: #000B13;
  line-height: 20px;
}
.c-h{
    max-height: 60vh;
    overflow-y: auto;
}
</style>