<template>
  <div class="box-bj">
    <PageHeader>
      <template v-slot:left>
        <DateSearch
          class="header-btn-list"
          @changeDateSearch="changeDateSearch(date)"
        />
        <HeaderBtn class="header-btn-list">查询</HeaderBtn>
        <HeaderBtn @click="handleHighSearch()" class="header-btn-list"
          >高级查询</HeaderBtn
        >
        <HeaderBtn @click="createRecord()" class="header-btn-list"
          >生成档案</HeaderBtn
        >
        <HeaderBtn @click="createSync()" class="header-btn-list"
          >档案同步</HeaderBtn
        >
      </template>
      <template v-slot:right>
        <HeaderTextBtn>导出凭证</HeaderTextBtn>
        <HeaderTextBtn
          @click="handlePrintPreview()"
          class="header-btn-list right-m"
          >打印</HeaderTextBtn
        >
      </template>
    </PageHeader>
    <main>
      <div class="main-left">
        <Left/>
      </div>
      <div class="main-right">
        <div class="account-card accountVoucher-box">
          <Headers
            @clickType="clickType"
            v-if="clickTypeName !== 'resourceMore'"
          ></Headers>
          <AccountVoucher v-if="!clickTypeName"></AccountVoucher>
          <TypeAccessory
            v-if="clickTypeName === 'accessory'"
            @back="back"
          ></TypeAccessory>
          <TypeContract
            v-if="clickTypeName === 'contract'"
            @back="back"
          ></TypeContract>
          <TypeInvoice
            v-if="clickTypeName === 'invoice'"
            @back="back"
          ></TypeInvoice>
          <TypeResource
            v-if="clickTypeName === 'resource'"
            @back="back"
          ></TypeResource>
          <ResourceFileMore
            v-if="clickTypeName === 'resourceMore'"
            @back="back"
          ></ResourceFileMore>
        </div>
        <div v-if="!clickTypeName" class="account-card accountVoucherItem-box">
          <AccountVoucherItem></AccountVoucherItem>
        </div>
        <div v-if="!clickTypeName" class="account-card resourceFile-box">
          <ResourceFile @viewMore="viewMore()"></ResourceFile>
        </div>
      </div>
    </main>
    <PrintfPreview ref="PrintfPreview" />
    <HighSearch ref="HighSearch" />
  </div>
</template>
<script>
import PageHeader from "@/components/page-header/index.vue";
import DateSearch from "@/components/search/date-search.vue";
import HeaderBtn from "@/components/btn/btn.vue";
import HeaderTextBtn from "@/components/btn/text-btn.vue";
import Headers from "./header.vue";
import AccountVoucher from "@/views/voucherManage/voucherDetail/accountVoucher.vue";
import AccountVoucherItem from "@/views/voucherManage/voucherDetail/accountVoucherItem.vue";
import ResourceFile from "@/views/voucherManage/voucherDetail/resourceFile/resourceFile.vue";
import TypeAccessory from "./typeAccessory.vue";
import TypeContract from "./typeContract.vue";
import TypeInvoice from "./typeInvoice.vue";
import TypeResource from "./typeResource.vue";
import ResourceFileMore from "./resourceFile/resourceFileMore.vue";
import HighSearch from "./components/highSearch.vue";
import PrintfPreview from "./components/printfPreview.vue";
import Left from "./left.vue"
export default {
  data() {
    return {
      clickTypeName: "",
    };
  },
  components: {
    PageHeader,
    HeaderBtn,
    HeaderTextBtn,
    DateSearch,
    Headers,
    AccountVoucher,
    AccountVoucherItem,
    ResourceFile,
    TypeAccessory,
    TypeContract,
    TypeInvoice,
    TypeResource,
    ResourceFileMore,
    HighSearch,
    PrintfPreview,
    Left
  },
  methods: {
    changeDateSearch(date) {},
    clickType(type) {
      switch (type) {
        case "accessory":
          this.clickTypeName = type;
          break;
        case "resource":
          this.clickTypeName = type;
          break;
        case "invoice":
          this.clickTypeName = type;
          break;
        case "contract":
          this.clickTypeName = type;
          break;
        case "prev":
          this.clickTypeName = "";
          break;
        case "next":
          this.clickTypeName = "";
          break;
      }
    },
    viewMore() {
      this.clickTypeName = "resourceMore";
    },
    back() {
      this.clickTypeName = "";
    },
    handlePrintPreview() {
      this.$refs["PrintfPreview"].isVisible = true;
    },
    handleHighSearch() {
      this.$refs["HighSearch"].isHighSearchVisible = true;
    },
    createRecord() {
      this.$message.success("已成功生成档案！");
    },
    createSync() {
      this.$message.success("档案同步成功！");
    },
  },
};
</script>
<style scoped lang="scss">
.box-bj {
  height: 100%;
  width: 100%;
}
.right-m {
  margin-right: $separation-size * 2;
}
main {
  width: 100%;
  height: calc(100% - 60px - $separation-size * 2);
  margin-top: $separation-size * 2;
  .main-left {
    float: left;
    width: 200px;
    height: 100%;
    background: #ffffff;
    border-radius: $border-radius;
  }
  .main-right {
    float: right;
    width: calc(100% - 200px - $separation-size * 2);
    height: 100%;
    overflow-y: auto;
  }
}

.account-card {
  width: 100%;
  height: auto;
  background-color: #ffffff;
  box-sizing: border-box;
  margin-bottom: $separation-size * 2;
  padding: $separation-size * 2;
  border-radius: $border-radius;
}
</style>