<template>
  <div>
    <el-table
      :data="tableData"
      style="width: 100%"
      :header-row-class-name="tableRowClassName"
      size="medium"
      tooltip-effect="dark"
      :border="border"
      :show-summary="showSummary"
    >
      <el-table-column v-if="isSelection" type="selection" width="80">
      </el-table-column>
      <el-table-column
        v-for="(item, index) in tableColumns.columns"
        :key="index"
        :prop="item.prop"
        :label="item.title"
        :width="item.width"
        show-overflow-tooltip
      />
      <!-- 操作按钮 -->
      <el-table-column
        v-if="tableColumns.operator"
        :prop="tableColumns.operator.prop"
        :label="tableColumns.operator.title"
        :width="tableColumns.operator.width"
      >
        <template slot-scope="scope">
          <TextBtn
            v-for="(item, index) in operatorList"
            class="btn-margin"
            :key="index"
            :size="'mini'"
          >
            <span @click="handleOperator(item, scope.row)">
              {{ item.title }}
            </span>
          </TextBtn>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import TextBtn from "@/components/btn/text-btn.vue";
export default {
  props: {
    tableColumns: {
      type: Object,
      default: () => {
        return {};
      },
    },
    tableData: {
      type: Array,
      default: () => {
        return [];
      },
    },
    operatorList: {
      type: Array,
      default: () => {
        return [];
      },
    },
    isSelection: {
      type: Boolean,
      default: false,
    },
    border:{
      type: Boolean,
      default: false,
    },
    showSummary:{
      type: Boolean,
      default: false
    }
  },
  data() {
    return {};
  },
  components: { TextBtn },
  methods: {
    tableRowClassName({ row, rowIndex }) {
      return "headerColor";
    },
    handleOperator(item, row) {
      this.$emit("handleOperator", item, row);
    },
  },
};
</script>
<style scoped lang="scss">
.el-table >>> .headerColor th {
  background: #f4f5f8 !important;
}
 ::v-deep .el-table th.el-table__cell{
  background: #f4f5f8 !important;
}
.btn-margin {
  margin: 0 8px;
}
</style>