<!--发票管理-->
<template>
  <div class="container-box">
    <PageHeader>
      <template v-slot:left>
        <InputSearch
          :placeholder="'请输入发票号码'"
          @changeInputSearch="changeInputSearch(inputVal)"
        ></InputSearch>
        <DateSearch
          class="header-btn-list"
          @changeDateSearch="changeDateSearch(date)"
        />
        <SelectSearch
          class="header-btn-list"
          :placeholder="'请选择发票类型'"
          :dataListOpt="dataListOpt"
          @changeSelectSearch="changeSelectSearch(selectVal)"
        ></SelectSearch>
        <HeaderBtn class="header-btn-list">查询</HeaderBtn>
      </template>
      <template v-slot:right>
        <HeaderBtn :isPlain="false" @click="add()" class="header-btn-list"
          >新增</HeaderBtn
        >
        <HeaderBtn class="header-btn-list" :type="'danger'">批量删除</HeaderBtn>
      </template>
    </PageHeader>
    <!-- 主体内容 -->
    <main>
      <BaseTable
        :isSelection="true"
        :tableData="tableData"
        :tableColumns="tableColumns"
        :operatorList="operatorList"
        @handleOperator="handleOperator"
      >
      </BaseTable>
    </main>
    <!-- 添加修改弹窗 -->
    <Forms ref="Forms" :title="formTitle" />
  </div>
</template>
<script>
import PageHeader from "@/components/page-header/index.vue";
import InputSearch from "@/components/search/input-search.vue";
import SelectSearch from "@/components/search/select-search.vue";
import DateSearch from "@/components/search/date-search.vue";
import HeaderBtn from "@/components/btn/btn.vue";
import BaseTable from "@/components/table/base-table.vue";
import Forms from "./form.vue";
export default {
  data() {
    return {
      formTitle: "",
      tableColumns: {
        columns: [
          {
            prop: "seqNum",
            key: "seqNum",
            title: "序号",
            width: "50",
          },
          {
            prop: "bianhao",
            key: "bianhao",
            title: "发票类型",
          },
          {
            prop: "bianhao1",
            key: "bianhao1",
            title: "开票日期",
          },
          {
            prop: "bianhao2",
            key: "bianhao2",
            title: "发票号码",
          },
          {
            prop: "bianhao3",
            key: "bianhao3",
            title: "合计金额",
          },
          {
            prop: "bianhao4",
            key: "bianhao4",
            title: "税率",
          },
          {
            prop: "bianhao5",
            key: "bianhao5",
            title: "税额",
          },
          {
            prop: "bianhao6",
            key: "bianhao6",
            title: "价税合计",
          },
        ],
        operator: {
          prop: "operator",
          key: "operator",
          title: "操作",
          width: "200",
        },
      },
      tableData: [
        {
          seqNum: 1,
          bianhao: "增值税普通发票",
          bianhao1: "2021-12-25",
          bianhao2: "3435565767",
          bianhao3: "14,032.12",
          bianhao4: "3%",
          bianhao5: "14,032.12",
          bianhao6: "14,032.12",
        },
      ],
      operatorList: [
        { title: "查看凭证", key: "viewVoucher" },
        { title: "修改", key: "edit" },
        { title: "删除", key: "delete" },
      ],
      dataListOpt: [
        {
          value: "1",
        },
      ],
    };
  },
  components: {
    PageHeader,
    InputSearch,
    SelectSearch,
    HeaderBtn,
    DateSearch,
    BaseTable,
    Forms,
  },
  methods: {
    changeDateSearch(date) {},
    changeInputSearch(inputVal) {},
    changeSelectSearch(selectVal) {},
    add() {
      this.$refs["Forms"].isVisible = true;
      this.formTitle = "新增";
    },
    handleOperator(item, row) {
      switch (item.key) {
        case "viewVoucher":
          break;
        case "edit":
          this.$refs["Forms"].isVisible = true;
          this.formTitle = item.title;
          break;
        case "delete":
          break;
      }
    },
  },
};
</script>
<style scoped lang="scss">
</style>