<!--tag标签-->
<template>
  <div class="tags-view">
    <scrollPane ref="scrollPane" class="tags-view-wrapper">
      <el-tag v-for="(tag, index) in tagList" :key="index" closable size="small" :class="{ active: tag.isActive }" @click="callTag(tag)" @close="handleClose(tag)" >{{ tag.title }}</el-tag>
    </scrollPane>
  </div>
</template>
<script>
import scrollPane from "../scroll-pane.vue";
export default {
  components: { scrollPane },
  name: "TagsView",
  data() {
    return {
      top: 0,
      left: 0,
      selectedTag: {},
    };
  },
  computed: {
    tagList() {
      return this.$store.getters.tagList;
    },
  },
  created() {
  },
  methods: {
    // 点击某个tag
    callTag(tag) {
      if (!tag.isActive) {
        this.tagList.map((item) => {
          item.isActive = false;
        });
        tag.isActive = true;
        this.$router.push(tag.path);
        this.$store.commit("setTagList", this.tagList);
      }
    },
    // 关闭tag
    handleClose(tag) {
      if (this.tagList.length === 1) {
        this.$message.error("应至少保留一个标签页!");
      } else {
        if (tag.isActive) {
          // 当前要关闭的tag是active,改变active状态
          this.tagList.splice(tag.index, 1);
          this.tagList.map((item, index) => {
            item.index = index;
          });
          this.tagList[0].isActive = true;
          this.$router.push(this.tagList[0].path);
          this.$store.commit("setTagList", this.tagList);
        } else {
          // 当前要关闭的tag不是active,不改变active状态
          this.tagList.splice(tag.index, 1);
          this.tagList.map((item, index) => {
            item.index = index;
          });
          this.$store.commit("setTagList", this.tagList);
        }
      }
    },
    // 关闭当前tag
    closeCurrentTag(e) {
      this.handleClose(this.selectedTag);
    },
  },
};
</script>

<style scoped lang="scss">
/* 整体tag  */
.tags-view {
  overflow: auto;
  white-space: nowrap;
  background-color: #fff;
  height: 58px;
  display: flex;
  align-items: center;
}

/* 单独tag */
.el-tag {
  margin-left: 8px;
  display: inline-block;
  cursor: pointer;
  height: 36px;
  line-height: 36px;
  background: #f4f5f7;
  border-radius: 4px;
  color: #757a95;
  font-size: 14px;
  border: none;
}

/* 活跃状态的tag */
.el-tag.active {
  color: $color-primary;
  background:  #E7F4FF;;
}
.el-tag.active ::v-deep .el-icon-close {
  color: $color-primary;
}

