<template>
  <div class="date">
    <el-select v-model="searchSelect" :size="size" :placeholder="placeholder">
      <el-option
        v-for="(item,index) in dataListOpt"
        :key="index"
        :label="item.value"
        :value="item.value"
      >
      </el-option>
    </el-select>
  </div>
</template>
<script>
export default {
  props: {
    size: {
      type: String,
      default: "medium",
    },
    placeholder: {
      type: String,
      default: "请选择",
    },
    dataListOpt:{
        type:Array,
        default:()=>{
            return []
        }
    }
  },
  data() {
    return {
      searchSelect: "",
    };
  },
  methods: {
    changeSelectSearch(data) {
      this.$emit("changeSelectSearch", data);
    },
  },
};
</script>
<style scoped lang="scss">
.date {
  display: inline-block;
}
</style>