<!--凭证管理-->
<template>
  <div class="container-box">
    <PageHeader>
      <template v-slot:left>
        <DateSearch @changeDateSearch="changeDateSearch(date)" />
        <HeaderBtn class="header-btn-list">查询</HeaderBtn>
        <HeaderBtn @click="handleHighSearch()" class="header-btn-list"
          >高级查询</HeaderBtn
        >
        <HeaderBtn @click="handleSync()" class="header-btn-list"
          >同步</HeaderBtn
        >
        <HeaderBtn @click="createRecord()" class="header-btn-list"
          >生成档案</HeaderBtn
        >
        <HeaderBtn @click="createSync()" class="header-btn-list"
          >档案同步</HeaderBtn
        >
        <HeaderBtn class="header-btn-list" :type="'danger'">批量删除</HeaderBtn>
      </template>
      <template v-slot:right>
        <HeaderTextBtn>导出凭证</HeaderTextBtn>
        <HeaderTextBtn @click="handlePrintPreview()" class="header-btn-list"
          >打印</HeaderTextBtn
        >
      </template>
    </PageHeader>
    <!-- 统计数据 -->
    <div class="statistics-list">
      <span class="base-margin-left-space">共计凭证数: 17225</span>
      <span class="base-margin-left-space">共计附件张数：225</span>
      <span class="base-margin-left-space">财务会计合计：36,225.06</span>
      <span class="base-margin-left-space">预算会计合计：36,225.06</span>
    </div>
    <!-- 主体内容 -->
    <main>
      <BaseTable
        :isSelection="true"
        :tableData="tableData"
        :tableColumns="tableColumns"
        :operatorList="operatorList"
        @handleOperator="handleOperator"
      >
      </BaseTable>
    </main>
    <!-- 同步 -->
    <Dialog
      :width="'40%'"
      :title="'同步'"
      :visible="isSyncVisible"
      @submit="submit"
      @cancel="cancel"
    >
      <template>
        <el-row class="sync-left">上传</el-row>
        <el-upload
          style="display: inline-block"
          action="https://jsonplaceholder.typicode.com/posts/"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :before-remove="beforeRemove"
          :on-exceed="handleExceed"
          :file-list="syncFileList"
        >
          <HeaderBtn>点击上传</HeaderBtn>
        </el-upload>
        <span class="sync-left"> 注：支持国标xml格式的zip/rar压缩包</span>
      </template>
    </Dialog>
    <PrintfPreview ref="PrintfPreview" />
    <HighSearch ref="HighSearch" />
  </div>
</template>
<script>
import PageHeader from "@/components/page-header/index.vue";
import DateSearch from "@/components/search/date-search.vue";
import HeaderBtn from "@/components/btn/btn.vue";
import HeaderTextBtn from "@/components/btn/text-btn.vue";
import BaseTable from "@/components/table/base-table.vue";
import Dialog from "@/components/dialog/index.vue";
import HighSearch from "./voucherDetail/components/highSearch.vue";
import PrintfPreview from "./voucherDetail/components/printfPreview.vue";

export default {
  data() {
    return {
      searchDate: "",
      tableColumns: {
        columns: [
          {
            prop: "seqNum",
            key: "seqNum",
            title: "序号",
          },
          {
            prop: "bianhao",
            key: "bianhao",
            title: "凭证编号",
          },
          {
            prop: "bianhao1",
            key: "bianhao1",
            title: "凭证摘要",
          },
          {
            prop: "bianhao2",
            key: "bianhao2",
            title: "财务会计金额",
          },
          {
            prop: "bianhao3",
            key: "bianhao3",
            title: "预算会计金额",
          },
          {
            prop: "bianhao4",
            key: "bianhao4",
            title: "发票张数",
          },
          {
            prop: "bianhao5",
            key: "bianhao5",
            title: "附件张数",
          },
          {
            prop: "bianhao6",
            key: "bianhao6",
            title: "生成档案",
          },
          {
            prop: "bianhao7",
            key: "bianhao7",
            title: "档案同步",
          },
        ],
        operator: { prop: "operator", key: "operator", title: "操作" },
      },
      tableData: [
        {
          seqNum: 1,
          bianhao: "JZ-01-001",
          bianhao1: "电费发票冲账",
          bianhao2: "14,032.12",
          bianhao3: "14,032.12",
          bianhao4: "3",
          bianhao5: "5",
          bianhao6: "是",
          bianhao7: "是",
        },
        {
          seqNum: 2,
          bianhao: "JZ-01-001",
          bianhao1: "电费发票冲账",
          bianhao2: "14,032.12",
          bianhao3: "14,032.12",
          bianhao4: "3",
          bianhao5: "5",
          bianhao6: "是",
          bianhao7: "是",
        },
        {
          seqNum: 3,
          bianhao: "JZ-01-001",
          bianhao1: "电费发票冲账",
          bianhao2: "14,032.12",
          bianhao3: "14,032.12",
          bianhao4: "3",
          bianhao5: "5",
          bianhao6: "是",
          bianhao7: "是",
        },
        {
          seqNum: 4,
          bianhao: "JZ-01-001",
          bianhao1: "电费发票冲账",
          bianhao2: "14,032.12",
          bianhao3: "14,032.12",
          bianhao4: "3",
          bianhao5: "5",
          bianhao6: "是",
          bianhao7: "是",
        },
        {
          seqNum: 5,
          bianhao: "JZ-01-001",
          bianhao1: "电费发票冲账",
          bianhao2: "14,032.12",
          bianhao3: "14,032.12",
          bianhao4: "3",
          bianhao5: "5",
          bianhao6: "是",
          bianhao7: "是",
        },
      ],
      operatorList: [{ title: "凭证详情", key: "detailVoucher" }],
      isSyncVisible: false,
      syncFileList: [],
    };
  },
  components: {
    PageHeader,
    HeaderBtn,
    HeaderTextBtn,
    DateSearch,
    BaseTable,
    Dialog,
    HighSearch,
    PrintfPreview,
  },
  methods: {
    changeDateSearch(date) {},
    handleOperator(item, row) {
      if (item.key === "detailVoucher") {
        this.$router.push("/voucherManage/voucherDetail");
      }
    },
    handleSync() {
      this.isSyncVisible = true;
    },
    cancel() {
      this.isSyncVisible = false;
    },
    submit() {
      this.cancel();
    },
    handlePreview() {},
    handleRemove() {},
    beforeRemove() {},
    handleExceed() {},
    handlePrintPreview() {
      this.$refs["PrintfPreview"].isVisible = true;
    },
    handleHighSearch() {
      this.$refs["HighSearch"].isHighSearchVisible = true;
    },
    createRecord() {
      this.$message.success("已成功生成档案！");
    },
    createSync() {
      this.$message.success("档案同步成功！");
    },
  },
};
</script>
<style scoped lang="scss">
.statistics-list {
  color: #757a95;
  font-size: 14px;
  font-weight: 400;
  text-align: right;
  margin-bottom: $separation-size;
}
.sync-left {
  margin: 0 0 $separation-size 0;
  font-size: 14px;
  font-weight: 400;
  color: #7984a7;
}
</style>