import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '@/views/login.vue'
import Layout from '@/components/layout/index.vue'

import Home from '@/views/home/index.vue'
import VoucherManage from '@/views/voucherManage/index.vue'
import VoucherDetail from '@/views/voucherManage/voucherDetail/index.vue'
import InvoiceManage from '@/views/invoiceManage/index.vue'
import LedgerManage from '@/views/ledgerManage/index.vue'
import ReportManage from '@/views/reportManage/index.vue'
import ContractManage from '@/views/contractManage/index.vue'
import ResourceManage from '@/views/resourceManage/index.vue'
import SysSetting from '@/views/sysSetting/index.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: "/",
    meta: {
      title: "登录",
      noCache: true,
    },
    component: Login,
  },
  {
    path: "/login",
    meta: {
      title: "登录",
      noCache: true,
    },
    component: Login,
  },
  {
    path: '/home',
    component: Layout,
    children: [
      {
        path: '',
        component: Home,
        name: 'Home',
        meta: { title: '系统首页', icon: 'documentation', affix: true }
      }
    ]
  },
  {
    path: '/voucherManage',
    component: Layout,
    children: [
      {
        path: 'index',
        component: VoucherManage,
        name: 'VoucherManage',
        meta: { title: '凭证管理', icon: 'documentation', affix: true }
      },
      {
        path: 'voucherDetail',
        component: VoucherDetail,
        name: 'VoucherDetail',
        meta: { title: '凭证详情', icon: 'documentation', affix: true }
      },
    ]
  },
  {
    path: '/invoiceManage',
    component: Layout,
    children: [
      {
        path: 'index',
        component: InvoiceManage,
        name: 'InvoiceManage',
        meta: { title: '发票管理', icon: 'documentation', affix: true }
      }
    ]
  },
  {
    path: '/ledgerManage',
    component: Layout,
    children: [
      {
        path: 'index',
        component: LedgerManage,
        name: 'LedgerManage',
        meta: { title: '账簿管理', icon: 'documentation', affix: true }
      }
    ]
  },
  {
    path: '/reportManage',
    component: Layout,
    children: [
      {
        path: 'index',
        component: ReportManage,
        name: 'ReportManage',
        meta: { title: '报表管理', icon: 'documentation', affix: true }
      }
    ]
  },
  {
    path: '/contractManage',
    component: Layout,
    children: [
      {
        path: 'index',
        component: ContractManage,
        name: 'ContractManage',
        meta: { title: '合同管理', icon: 'documentation', affix: true }
      }
    ]
  },
  {
    path: '/resourceManage',
    component: Layout,
    children: [
      {
        path: 'index',
        component: ResourceManage,
        name: 'ResourceManage',
        meta: { title: '资源管理', icon: 'documentation', affix: true }
      }
    ]
  },
  {
    path: '/sysSetting',
    component: Layout,
    children: [
      {
        path: 'index',
        component: SysSetting,
        name: 'SysSetting',
        meta: { title: '系统设置', icon: 'documentation', affix: true }
      }
    ]
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})
// const VueRouterPush = router.prototype.push
// router.prototype.push = function push (to) {
//   return VueRouterPush.call(this, to).catch(err => err)
// }
export default router
