<template>
  <div class="container-box">
    <PageHeader>
      <template v-slot:left>
        <InputSearch
          :placeholder="'请输入文件名称或编号'"
          @changeInputSearch="changeInputSearch(inputVal)"
        />
        <HeaderBtn class="header-btn-list">查询</HeaderBtn>
      </template>
      <template v-slot:right>
        <HeaderBtn @click="add()" :isPlain="false" class="header-btn-list"
          >新增</HeaderBtn
        >
      </template>
    </PageHeader>
    <!-- 主体内容 -->
    <main>
      <BaseTable
        :tableData="tableData"
        :tableColumns="tableColumns"
        :operatorList="operatorList"
        @handleOperator="handleOperator"
      >
      </BaseTable>
    </main>
    <!-- 添加修改弹窗 -->
    <Forms ref="Forms" :title="formTitle" />
  </div>
</template>
<script>
import PageHeader from "@/components/page-header/index.vue";
import InputSearch from "@/components/search/input-search.vue";
import HeaderBtn from "@/components/btn/btn.vue";
import BaseTable from "@/components/table/base-table.vue";
import Forms from "./form.vue";
export default {
  data() {
    return {
      formTitle: "",
      tableColumns: {
        columns: [
          {
            prop: "seqNum",
            key: "seqNum",
            title: "序号",
            width: "50",
          },
          {
            prop: "bianhao",
            key: "bianhao",
            title: "文件编号",
          },
          {
            prop: "bianhao1",
            key: "bianhao1",
            title: "文件名称",
          },
          {
            prop: "bianhao2",
            key: "bianhao2",
            title: "下发日期",
          },
          {
            prop: "bianhao3",
            key: "bianhao3",
            title: "备注说明",
          },
        ],
        operator: {
          prop: "operator",
          key: "operator",
          title: "操作",
          width: "280",
        },
      },
      tableData: [
        {
          seqNum: 1,
          bianhao: "3435565767",
          bianhao1: "关于印发《兵团机关培训管理》",
          bianhao2: "2021-12-27",
          bianhao3: "兵团机关单位各部门",
        },
        {
          seqNum: 2,
          bianhao: "3435565767",
          bianhao1: "关于印发《兵团机关培训管理》",
          bianhao2: "2021-12-27",
          bianhao3: "兵团机关单位各部门",
        },
      ],
      operatorList: [
        { title: "查看凭证", key: "viewVoucher" },
        { title: "查看附件", key: "viewResource" },
        { title: "修改", key: "edit" },
        { title: "删除", key: "delete" },
      ],
    };
  },
  components: {
    PageHeader,
    InputSearch,
    HeaderBtn,
    BaseTable,
    Forms,
  },
  methods: {
    changeInputSearch(inputVal) {},
    add() {
      this.$refs["Forms"].isVisible = true;
      this.formTitle = "新增";
    },
    handleOperator(item, row) {
      switch (item.key) {
        case "viewVoucher":
          break;
        case "viewResource":
          break;
        case "edit":
          this.$refs["Forms"].isVisible = true;
          this.formTitle = item.title;
          break;
        case "delete":
          break;
      }
    },
  },
};
</script>
<style scoped lang="scss">
</style>