<template>
  <div class="box">
    <div class="left">
      <sysTitle />
      <i :class="isCollapse?'el-icon-s-unfold':'el-icon-s-fold'" @click="handleCollapse()" class="collapse-icon"></i>
      <el-select
        v-model="companyVal"
        disabled
        class="base-margin-left-space"
        size="small"
        placeholder="请选择"
      >
        <el-option
          v-for="item in companyOpt"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
      <el-date-picker
        v-model="dateVal"
        disabled
        class="base-margin-left-space"
        size="small"
        type="date"
        placeholder="选择日期"
      ></el-date-picker>
    </div>
    <div class="right">
      <div class="full-icon-box base-pointer" @click="toggleFullscreen">
        <i :class="isCollapse?'el-icon-full-screen':'el-icon-full-screen'" ></i>
        <!-- <img :src="fullIcon" alt="" /> -->
      </div>
      <div class="user base-margin-left-space">
        <el-avatar :src="userLogo"></el-avatar>
      </div>
      <el-dropdown
        class="base-margin-left-space base-pointer"
        trigger="click"
        @command="mineCheck"
      >
        <span class="el-dropdown-link">
          赵筱筱<i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="个人信息">个人信息</el-dropdown-item>
          <el-dropdown-item command="修改密码">修改密码</el-dropdown-item>
          <el-dropdown-item command="退出登录">退出登录</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <!-- 个人信息 -->
    <Dialog
      :width="'550px'"
      :title="'个人信息'"
      :visible="isPersonalInfoVisible"
      @submit="submitPersonalInfo"
      @cancel="cancelPersonalInfo"
    >
      <template>
        <el-form
          :label-position="'top'"
          label-width="80px"
          :model="personalInfoForm"
          size="medium"
        >
          <el-form-item
            v-for="(item, index) in personalInfoFormLabelData"
            :key="index"
            :label="item.label"
          >
            <el-input
              :disabled="item.disabled"
              v-model="personalInfoForm[item.key]"
            ></el-input>
          </el-form-item>
        </el-form>
      </template>
    </Dialog>
    <!-- 修改密码 -->
    <Dialog
      :width="'550px'"
      :title="'修改密码'"
      :visible="isEditPasswordVisible"
      @submit="submitEditPassword"
      @cancel="cancelEditPassword"
    >
      <template>
        <el-form
          :label-position="'top'"
          label-width="80px"
          :model="editPasswordForm"
          size="medium"
        >
          <el-form-item
            v-for="(item, index) in editPasswordFormLabelData"
            :key="index"
            :label="item.label"
          >
            <el-input
              :disabled="item.disabled"
              v-model="editPasswordForm[item.key]"
            ></el-input>
          </el-form-item>
        </el-form>
      </template>
    </Dialog>
  </div>
</template>

<script>
import sysTitle from "@/components/sys-title.vue";
import Dialog from "@/components/dialog/index.vue";
export default {
  name: "Header",
  components: { sysTitle, Dialog },
  data() {
    return {
      personalInfoFormLabelData: [
        { label: "我的账号", key: "code", disabled: true },
        { label: "用户名称", key: "code1", disabled: false },
        { label: "联系电话", key: "code2", disabled: false },
        { label: "单位名称", key: "code3", disabled: false },
      ],
      personalInfoForm: {
        code: "123456789",
        code1: "",
        code2: "",
        code3: "",
      },
      editPasswordFormLabelData: [
        { label: "新密码", key: "code", disabled: false },
        { label: "再次确认新密码", key: "code1", disabled: false },
      ],
      editPasswordForm: {},
      companyOpt: [
        {
          value: "西安凡数创新信息技术有限公司",
          label: "西安凡数创新信息技术有限公司",
        },
      ],
      companyVal: "西安凡数创新信息技术有限公司",
      dateVal: "2022-1-5",
      fullIcon: require("../../assets/imgs/header_fullscreen_expand.png"),
      userLogo: require("../../assets/imgs/header_woman.png"),
      toggleLeft: require("../../assets/imgs/header_toggle_left.png"),
      isPersonalInfoVisible: false,
      isEditPasswordVisible: false,
      isCollapse:false
    };
  },
  methods: {
    handleCollapse(){
      this.isCollapse = !this.isCollapse
      this.$emit("handleCollapse")
    },
    // 点击列表
    mineCheck(command) {
      if (command === "个人信息") {
        this.personalInfo();
      } else if (command === "修改密码") {
        this.editPassword();
      } else if (command === "退出登录") {
        this.personalLogout();
      }
    },
    //======================================= 个人信息
    personalInfo() {
      this.isPersonalInfoVisible = true;
    },
    submitPersonalInfo() {
      this.cancelPersonalInfo();
    },
    cancelPersonalInfo() {
      this.isPersonalInfoVisible = false;
    },
    // ====================================修改密码
    editPassword() {
      this.isEditPasswordVisible = true;
    },
    submitEditPassword() {
      this.cancelEditPassword();
    },
    cancelEditPassword() {
      this.isEditPasswordVisible = false;
    },
    //=====================================退出登录
    personalLogout() {
      this.$message.success("已退出！")
      this.$router.push({ path: "/" });
      sessionStorage.clear();
    },
    //================================== 全屏
    toggleFullscreen() {
      let _this = this;
      let el = document.documentElement;
      if (document.fullscreenElement === null) {
        _this.openFullscreen(el);
      } else {
        _this.quitFullscreen();
      }
    },
    openFullscreen(element) {
      if (element.requestFullscreen) {
        element.requestFullscreen();
      } else if (element.webkitRequestFullScreen) {
        element.webkitRequestFullScreen();
      } else if (element.mozRequestFullScreen) {
        element.mozRequestFullScreen();
      } else if (element.msRequestFullscreen) {
        element.msRequestFullscreen();
      }
    },
    quitFullscreen() {
      if (document.exitFullscreen) {
        document.exitFullscreen();
      } else if (document.webkitCancelFullScreen) {
        document.webkitCancelFullScreen();
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen();
      } else if (document.msExitFullscreen) {
        document.msExitFullscreen();
      }
    },
  },
};
</script>

<style lang="scss" scoped >
.box {
  width: 100%;
  height: 60px;
  background-color: $background-primary;
  padding: 0 32px;
  font-size: 20px;
  box-sizing: border-box;
  .left {
    color: #ffffff;
    height: 100%;
    float: left;
    display: flex;
    align-items: center;
  }
  .right {
    height: 100%;
    float: right;
    display: flex;
    align-items: center;
    .full-icon-box {
      width: 26px;
      height: 26px;
      background: #ffffff;
      border: 1px solid #eaecef;
      border-radius: 4px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .user {
      width: 30px;
      height: 30px;
      .el-avatar {
        width: 100%;
        height: 100%;
      }
    }
    .el-dropdown-link {
      color: #ffffff;
    }
  }
}
::v-deep .el-form--label-top .el-form-item__label {
  padding: 0;
}
.collapse-icon{
  margin-left: $separation-size;
}
</style>